	var tha,js;
  export default {
    components: {
      cmdTransition,
      cmdInput
    },

    data() {
      return {
		  second:0,
        mobile: {
          phone: '',
          code: '',
          passwordOne: '',
          passwordTwo: ''
        },
        passwordReg: /^\w+$/,
        phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
			regphone: false,
			//phoneReg: /^1[3456789]\d{9}$/,
        safety: {
          time: 60,
          state: false,
          interval: ''
        },
        modifyMobile: false,
		str:'',
		
		havecode:false
      };
    },
	computed: {
		yanzhengma() {
			if (this.second == 0) {
				return '获取验证码';
			} else {
				if (this.second < 10 && this.second >= 0) {
					return '重新获取0' + this.second;
				} else if (this.second >= 10) {
					return '重新获取' + this.second;
				} else {
					return '重新获取';
				}
			}
		}
	},
    watch: {
      /**
       * 监听表单数值
       */
      mobile: {
        handler(newValue) {
          if (this.phoneReg.test(newValue.phone) && newValue.code.length == 4 && newValue.passwordOne.length >= 8 &&
            newValue.passwordOne == newValue.passwordTwo) {
            this.modifyMobile = true;
          } else {
            this.modifyMobile = false;
          }
        },
        deep: true
      }
    },
onLoad(){
			tha = this;
		},
    methods: {
		returnpage(){
			uni.navigateTo({
			  url:'/pages/user/info/info'
			});
		},
      /**
       * 提交按钮点击执行
       */
      fnModify() {
		if(this.mobile.passwordOne==''||this.mobile.passwordTwo==''){
			uni.showToast({title:"请输入密码",icon:'none'});
		}
		if(this.mobile.passwordOne!=this.mobile.passwordTwo){
			uni.showToast({title:"密码不相同",icon:'none'});
		}
		if(this.mobile.code.toLowerCase()!=this.str.toLowerCase()){
			uni.showToast({title:"验证码错误",icon:'none'});
		}
        console.log(JSON.stringify(this.mobile));
		let data={
			phone:this.mobile.phone,
			password:this.mobile.passwordOne,
			code:this.mobile.code,
		};
		setNewPass(data).then(res=>{
			if(res.data.code==200){
				uni.showToast({title:res.data.msg});
				this.returnpage();
				//setTimeout(this.returnpage(),4000);
			}else{
				uni.showToast({title:res.data.msg,icon:'none'});
			}
		})
      },
		prop_phone(value) {
			if (value !== '' && value !== null) {
				var reg = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/;
				if (!reg.test(value)) {
					uni.showToast({
						icon: 'none',
						title: '手机号码不合法'
					});
					this.regphone = false;
					return;
				} else {
					this.regphone = true;
				}
			} else {
				uni.showToast({
					icon: 'none',
					title: '手机号码不能为空'
				});
				this.regphone = false;
			}
		},
      /**
       * 获取验证码
       */
	  getcode(){
	  	var that=this;
	  	if(this.mobile.phone==''){
	  		uni.showToast({
	  		    icon: 'none',
	  		    title: '请输入正确的手机号'
	  		});
	  		return;
	  	}
	  
	  	if(this.havecode)
	  	{
	  		uni.showToast({title:"请等待！"});
	  		return;
	  	}
	  	this.havecode=true;
	  	// this.havecode=false;
	  	//到计时
	  	
	  	this.second = 60;
	  	js = setInterval(function(){
	  		tha.second--;
	  		if(tha.second<=0){
	  			that.havecode=false;
	  			clearInterval(js)
	  		}
	  	},1000)
	  	
	  	getRegCode(this.mobile.phone).then(res=>{
	  		if(res.data.code==200)
	  		{
	  			this.str=res.data.data.str;
	  			uni.showToast({title:res.data.message});
	  			
	  		}else{
	  			uni.showToast({title:res.data.message,icon:'none'});
	  		}
	  	}).catch(error=>{
	      console.log(error)
	    });
	  
	  },
	  
